import React from "react";
import classes from "./Tittle.module.css";

const Tittle = () => {
	return (
		<div className={classes.tittle_wrapper}>
			<a
				href={"https://ru.wikipedia.org/wiki/_"}
				value={"Pomodoro "}
				target="_blank"
				rel="noopener noreferrer">
				<span>Pomodoro </span>
			</a>
		</div>
	);
};

export default Tittle;
